@import "../common/mixins";
@import "../common/variable";

.about-card {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: $colorWhite;
  background-color: $colorOrange;
  @include max-tablet {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  &__picture {
    width: 100%;
    max-width: r(250);
    -ms-flex-negative: 0;
        flex-shrink: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 0;
    img {
      aspect-ratio: 0.682 / 1;
      -o-object-fit: cover;
         object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
  picture {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 0;
  }
  &__main {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    grid-gap: r(30);
    padding: r(40);
    @include max-tablet {
      padding: r(16);
    }
  }
  &__title {
    font-size: r(38);
    font-weight: bold;
    color: currentColor;
    @include max-tablet {
      font-size: r(26);
    }
  }
}